import React from 'react'
import Project from '../components/Project.js';

import BikeImg from '../bike.jpg'
import ArcadeImg from '../arcade.jpg'
import CodingImg from '../coding.jpg'
import GameImg from '../game.jpg'

function ProjectGrid(){
	const link = window.innerWidth > 1000 ? "https://jeffhundred.github.io/Snake" : "https://github.com/jeffhundred/Snake"

	return(
		<section className = "project-grid">
			<Project 
				name="CORONAVIRUS TRACKER"
				image={BikeImg}
				description="Visualizing Covid-19 data by country as well as globally. Created with ReactJS."
				projectLink="https://jeffhundred.github.io/covid19/"
				codeLink="https://github.com/jeffhundred/covid19"
			/>
			<Project 
				name="SNAKE"
				image={ArcadeImg}
				description="Recreating the classic game Snake, using the p5.js library for Javascript."
				projectLink={link}
				codeLink="https://github.com/jeffhundred/Snake"
			/>
			<Project 
				name="MEME GENERATOR"
				image={CodingImg}
				description="Built with React.js and using the imgflip api to randomly generate memes."
				projectLink="https://jeffhundred.github.io/meme-generator"
				codeLink="https://github.com/jeffhundred/meme-generator"
			/>
			<Project 
				name="GAMING GUIDE"
				image={GameImg}
				description="An item synthesis guide for Kingdom Hearts 1.5 on PS4."
				projectLink="https://jeffhundred.github.io/KH-Farming-Guide/"
				codeLink="https://github.com/jeffhundred/KH-Farming-Guide"
			/>
		</section>
	)
}

export default ProjectGrid