import React, {useState, useEffect} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowCircleDown, } from '@fortawesome/free-solid-svg-icons'
import { faLinkedinIn, faGithub } from '@fortawesome/free-brands-svg-icons'

function BodyHeader(props) {

	const [page, setPage] = useState("Projects")
	const [linkSize, setLinkSize] = useState("2x")
	const [arrowSize, setArrowSize] = useState("6x")


	function projectClick(){
		document.getElementById("projectButton").classList.add("onPage");
		document.getElementById("resumeButton").classList.remove("onPage")
		setPage("Projects")
		props.callbackFunction("Projects")
	}

	function resumeClick(){
		document.getElementById("resumeButton").classList.add("onPage")
		document.getElementById("projectButton").classList.remove("onPage")
		setPage("Resume")
		props.callbackFunction("Resume")
	}

	useEffect(() => {

		function handleResize() {
			setLinkSize(window.innerWidth > 2000 ? "4x" : "2x");
			setArrowSize(window.innerWidth > 2000 ? "10x" : "6x");
		}

		document.getElementById("projectButton").classList.add("onPage")

		setLinkSize(window.innerWidth > 2000 ? "4x" : "2x");
		setArrowSize(window.innerWidth > 2000 ? "10x" : "6x");

		window.addEventListener('resize', handleResize);

		return () => window.removeEventListener('resize', handleResize);
	}, [])

	function arrowClick(){
		let screenHeight = window.innerHeight;
		window.scrollTo({left: 0, top: screenHeight , behavior: 'smooth'})
	}

	return(
		<section className = "project-header">
			<div className = "project-header-nav">
				<ul className = "site-nav">
					<li><h3 id="projectButton" onClick={projectClick}>Projects</h3></li>
					<li><h3 id="resumeButton" onClick={resumeClick}>Resume</h3></li>
				</ul>
				<ul className = "social-nav">
					<li><a href="https://www.github.com/jeffhundred" target="_blank"><FontAwesomeIcon icon={faGithub} size={linkSize}/></a></li>
					<li><a href="https://www.linkedin.com/in/benjamin-boyer28/" target="_blank"><FontAwesomeIcon icon={faLinkedinIn} size={linkSize}/></a></li>
				</ul>
			</div>
			<div className = "project-header-body">
				<h2>my {page}</h2>
				<FontAwesomeIcon className = "arrow" icon={faArrowCircleDown} size={arrowSize} onClick={arrowClick} />
			</div>
		</section>
	)
}

export default BodyHeader