import React from 'react'
import Resume from '../resume2020.pdf'

function ResumePage(){
	return(
		<section className="resumePage">
			<div className="resume-container">
				<embed 
					className="resume" 
					type="application/pdf" 
					src={Resume} 
					height= '100%'
					width='100%'
				/>
			</div>
		</section>
	)
}

export default ResumePage
