import React, { useState, useEffect } from 'react';
import BodyHeader from '../components/BodyHeader.js';
import ProjectGrid from '../components/ProjectGrid.js'
import ResumePage from '../components/ResumePage.js'
import Footer from '../components/Footer.js'

function Body(){
	const [page, setPage] = useState("Projects")
	const setPageFromChild=(childData) => {
		setPage(childData)
	}

	return(
		<div className="content">
			<BodyHeader callbackFunction = {setPageFromChild}/>
			{page=="Projects" ? <ProjectGrid /> : <ResumePage />}
			<Footer />
		</div>
	)
}

export default Body