import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes, faBars } from '@fortawesome/free-solid-svg-icons'
import { faLinkedinIn, faGithub } from '@fortawesome/free-brands-svg-icons'

import PDF from '../resume2020.pdf'

function MobileMenu() {

	function openMenu(){
		document.getElementById("mobileMenu").classList.remove("menuHidden")
		document.getElementById("mobileMenu").classList.add("menuDisplay")
	}

	function closeMenu(){
		document.getElementById("mobileMenu").classList.remove("menuDisplay")
		document.getElementById("mobileMenu").classList.add("menuHidden")
	}

	return(
		<nav>
			<button className="mobile-toggle" onClick={openMenu}><FontAwesomeIcon icon={faBars} size="2x" className="menu-button"/></button>
			<div className="mobile-menu" id="mobileMenu">
			<FontAwesomeIcon icon={faTimes} size="2x" className="closeMenu" onClick={closeMenu}/>
				<nav className="menu-wrapper">
					<ul className="mobile-site-nav">
						<li><a href={PDF} target="_blank">RESUME</a></li>
						<li><a href="https://jeffhundred.github.io/my-portfolio/">PROJECTS</a></li>
					</ul>
					<ul className="mobile-social-nav">
						<li><a href="https://www.github.com/jeffhundred" target="_blank"><FontAwesomeIcon icon={faGithub} size="lg"/></a></li>
						<li><a href="https://www.linkedin.com/in/benjamin-boyer28/" target="_blank"><FontAwesomeIcon icon={faLinkedinIn} size="lg"/></a></li>
					</ul>
				</nav>
			</div>
		</nav>
	)
}

export default MobileMenu