import React from 'react';

function Project(props){

	return(
		<div className="project">
			<h3>{props.name}</h3>
			<img src={props.image}/>
			<p>{props.description}</p>
			<ul>
				<li><a href={props.projectLink} target="_blank">THE PROJECT</a></li>
				<li><a href ={props.codeLink}target="_blank">THE CODE</a></li>
			</ul>
		</div>
	)
}

export default Project